export default class SmoothScroll{
	constrctor() {
		this.canEdit = false;
	}
	checkCanEdit(){
		if("CCM_EDIT_MODE" in window){
			this.canEdit = CCM_EDIT_MODE;
		}
		return this.canEdit;
	}
	exec(){
		this.checkCanEdit();
		if(!this.canEdit){
			$('a[href*="#"]').not(".nosms").click(function() {
			let speed = 600, // ()
			href = $(this).prop("href"), //
			hrefPageUrl = href.split("#")[0], //#URL
			currentUrl = location.href; //

			currentUrl = currentUrl.split("#")[0]; //#URL
			//#
			if(hrefPageUrl == currentUrl){

				//#
				href = href.split("#");
				href = href.pop();
				href = "#" + href;

				//
				let target = $(href == "#" || href == "" ? 'html' : href);
				if(target.length > 0){
					let position = target.offset().top; //target
					$('body,html').animate({scrollTop: position}, speed, 'swing', function() {
						//()
						if(href != "#top" && href !="#") {
							location.href = href;
						}
					});
				}

				if($('.spnavi').css('display') == 'block'){
					$('.btn_sp_navi').click();
				}

				return false;
			}

			});
		}
	}
}
